
.App {
  text-align: center;
  display: grid;
  grid-template-columns: auto auto auto auto auto;
  grid-row-gap: 1vh;
  background-color: black
}

.App .project-swatch {
  display: grid;
  height: 18vh;
  width: 18vw;
  color: white;
  transition: all 0.4s ease-in-out;
  margin: 1vw;
  border-radius: 1vw
}

.App .project-swatch .group {
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

.App .project-swatch:hover {
  transform: scale(1.2);
  color: black;
}

.App .project-swatch:active {
  background-color: white
}

h2 {
  text-align: center;
  margin-top: 5vh;
  font-size: 3vh;
}

p, label {
  font-size: 2.25vh;
}

h1 {
  text-align: center;
  color: white;
}